import { Component,ViewChild,OnInit} from '@angular/core';
import{ CommonService } from './../services/common.service';
import { Router } from '@angular/router';
import {ActivatedRoute} from '@angular/router';
import { LoaderService } from './../services/loader.service';
import { NgModel, DefaultValueAccessor, NgControl } from '@angular/forms';
@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  providers :[CommonService]
})
export class NewsComponent implements OnInit {
  
  showLoader: boolean;
  url = '';
  news = [];
  comment = '';
  constructor(private _commonService:CommonService,private router: Router,private loaderService:LoaderService) { }

  ngOnInit() {
  	jQuery('.main-title-header').html('news');
    this.loaderService.display(true);
    this.loaderService.status.subscribe((val: boolean) => {
      this.showLoader = val;
    });
    this._commonService.getnews().map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
          this.url = CommonService.IMAGE_PATH;
          this.news = data.result;
          console.log(this.news)
          this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
    })
  }
  add_comment(data,event) {
    let target = event.target || event.srcElement || event.currentTarget;
    let comment = jQuery(target).closest('.input-group').find('textarea').val();
    let formdata = new FormData();
    console.log(comment)
    formdata.append('comment',JSON.stringify(comment));
    formdata.append('data',JSON.stringify(data));
    formdata.append('userdata',localStorage.getItem('currentUser'));
    this._commonService.add_comment(formdata).map(res => res.json())
      .toPromise().then((data) => {
        console.log(data);
        if(data.status == 'success'){
          var tr =   '<li><div class="comment-image"><img src="assets/images/comment-user.png" class="img-responsive" alt="Comment User"></div><div class="comment-text"><p class="">'+data.result.comment+'</p><span class="date sub-text">'+data.result.created_at+'</span></div> </li>';
          $(tr).insertBefore(jQuery(target).parents('li'));
        }
        })
      .catch((ex) => {
          console.log(ex);
      });
  }

}

@Component({
  selector: 'app-news',
  templateUrl: './create_news.component.html',
  providers :[CommonService,NgModel, DefaultValueAccessor]
})

export class CreateNewsComponent implements OnInit {
  errors :any;
  showLoader: boolean;
	categories = [];
	@ViewChild('fileInput') fileInput;
  article: string = '';
  category_id: string = '';
  subject: string = '';
  constructor(private _commonService:CommonService,private router: Router,private loaderService:LoaderService) {}

  ngOnInit() {
      this.loaderService.display(true);
      this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
  	jQuery('.main-title-header').html('Create news');
  	 this._commonService.getnewscatagories().map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
          this.categories = data.result;
          this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
      });
  }
  Create_news(values): void {
    this.loaderService.display(true);
		const formData = new FormData();
		formData.append('category_id', (values.category_id) ? values.category_id : '');
		formData.append('subject', (values.subject) ? values.subject : '');
		formData.append('article', (values.article) ? values.article : '');

		const fileBrowser = this.fileInput.nativeElement;
      formData.append('files', (fileBrowser.files[0]) ? fileBrowser.files[0] : '');
      this._commonService.create_news(formData).map(res => res.json())
      .toPromise().then((data) => {
      if(data.status == 'failure'){
        this.errors = data.error;
        this.loaderService.display(false);
      }else if(data.status == 'success'){
        this.router.navigate(['/news']);
        this.loaderService.display(false);

      }

      })
      .catch((ex) => {
          console.log(ex);
      });
	}

}



