import { Component ,OnInit} from '@angular/core';
import { Router} from '@angular/router';
import { LoaderService } from './../services/loader.service';
import { CommonService } from './../services/common.service';

@Component({
  selector: 'nav-dashboard',
  templateUrl: './dashboard.html',
  providers :[CommonService]
})
export class dashboardComponent{
	data_list:any=[];
	public data: any[];
	showLoader: boolean;
	post={};
	constructor( private router: Router,private loaderService: LoaderService,private _commonService:CommonService) {
		if (!localStorage.getItem('currentUser')) {
        	this.router.navigate(['/login']);
        }
  	}
  	ngOnInit(){
  		jQuery('.main-title-header').html('dashboard');
		this.loaderService.status.subscribe((val: boolean) => {
        	this.showLoader = val;
      	});
  		this.loaderService.display(true);
      	this._commonService.getdashboarddata(this.post).map(res => res.json())
	      .toPromise().then((data) => {
	        if(data.status == 'success'){
	          this.data_list = data.result;
	          this.loaderService.display(false);
	        }
	      })
	      .catch((ex) => {
	          console.log(ex);
	      });  	
	}
	search(value){
		this.loaderService.display(true);
		this._commonService.getdashboarddata(value).map(res => res.json())
	      .toPromise().then((data) => {
        if(data.status == 'success'){
          this.data_list = data.result;
          this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
      });
	}
}


