import { Component,OnInit} from '@angular/core';
import{ CommonService } from './../services/common.service';
import { Router } from '@angular/router';
import {ActivatedRoute} from '@angular/router';
import { LoaderService } from './../services/loader.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NGValidators } from 'ng-validators';
@Component({
  selector: 'nav-wwl',
  templateUrl: './receive_payment.html',
  providers :[CommonService]
  
})
export class ReceivePaymentComponent{
	data_list = [];
	showLoader: boolean;
	postdata: Object = {};
  search :any={}
	constructor(private _commonService:CommonService,private router: Router,private loaderService:LoaderService){

	}
	ngOnInit(){
		jQuery('.main-title-header').html('Receive Payment');
	 	this.loaderService.display(true);
    	this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
      this.search = '';
      this._commonService.get_all_receive_payment(this.search).map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
          this.data_list = data.result;
          this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
      });
	}
	receive(id){
		this.loaderService.display(true);
		this.postdata = { id:id, amount: $('#'+id).val()};
		this._commonService.receive_payment(this.postdata).map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
        	this.data_list = data.result;
        }else if(data.status == 'failure'){
        	$('.error_class').html('');
        	$('#error_'+id).html(data.error.amount);
        }
        this.loaderService.display(false);
      })
      .catch((ex) => {
          console.log(ex);
      });
	}
  receive_payment_search(formdata){
    this.loaderService.display(true);
    this._commonService.get_all_receive_payment(formdata.search).map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
          this.data_list = data.result;
          this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
      });
  }
}


