import { Component,OnInit } from '@angular/core';
import { CommonService } from './../services/common.service';
import { Router } from '@angular/router';
import {ActivatedRoute} from '@angular/router';
import { LoaderService } from './../services/loader.service';

@Component({
  selector: 'nav-df',
  templateUrl: './payment_receipt.html',
  providers :[CommonService]
})
export class PaymentReceiptComponent implements OnInit{
	Payments = [];
	data_list = [];
  search :any = {};
  	showLoader: boolean;

	constructor(private _commonService:CommonService,private loaderService:LoaderService,private router: Router){};
   	ngOnInit(){
   		jQuery('.main-title-header').html('Payment Receipt');
   		this.loaderService.display(true);
    	this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
        this.search = '';
        this._commonService.receive_receipt(this.search).map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
          	this.data_list = data.result;
          	this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
      });
   }
   payment_receipt_search(formdata){
      this.loaderService.display(true);
        this._commonService.receive_receipt(formdata.search).map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
            this.data_list = data.result;
            this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
      });
   }
}


