import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {NG2DataTableModule} from "angular2-datatable-pagination";
import { BsDatepickerModule } from 'ngx-bootstrap';
import * as $ from 'jquery';
import { ReactiveFormsModule } from '@angular/forms';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
// import { MaterialModule } from '@angular/material';

import { fakeBackendProvider } from './_helpers/fake-backend';
import { MockBackend, MockConnection } from '@angular/http/testing';
import { BaseRequestOptions } from '@angular/http';
import { AppComponent }  from './app.component';
import { HeaderComponent }  from './header/app.header';
import { OrderComponent }  from './order_form/app.order_form';
import { CreateorderComponent }  from './create_order/app.create_order';
import { ReceivePaymentComponent }  from './receive_payment/app.receive_payment';
import { PaymentReceiptComponent }  from './payment_receipt/app.payment_receipt';
// import { CreateNewsComponent }  from './create_news/app.create_news';
import { SearchPartyComponent }  from './search_party/app.search_party';
import { login }  from './login/app.login';
import { navComponent }  from './navigation/app.nav';
import { dashboardComponent }  from './dashboard/app.dashboard';
import { LogoutComponent }  from './logout/app.logout';
import { AlertComponent } from './_directives/alert.component';
import { AlertService} from './services/alert.services';
import { AuthenticationService} from './services/authentication.service';
import { AuthGuard } from './_guards/auth.guard';
import { UserService } from './services/user.service';
import { NameService } from './services/name.service';
import { ManageMembersComponent } from './manage-members/manage-members.component';
import { CreateManageMembersComponent } from './manage-members/manage-members.component';
import { EditMembersComponent } from './manage-members/manage-members.component';
import { LoaderComponent } from './loader/loader.component';
import { LoaderService } from './services/loader.service';
import { ManageAgentsComponent } from './manage-agents/manage-agents.component';
import { CreateManageAgentsComponent } from './manage-agents/manage-agents.component';
import { EditAgentsComponent } from './manage-agents/manage-agents.component';
import { ServerComponentComponent } from './server-component/server-component.component';
import { NewsComponent } from './news/news.component';
import { CreateNewsComponent } from './news/news.component';

const appRoutes: Routes = [
   { path: '', component: dashboardComponent },
   { path: 'OrderComponent', component: OrderComponent },
   { path: 'CreateorderComponent', component: CreateorderComponent },
   { path: 'receive_payment', component: ReceivePaymentComponent },
   { path: 'PaymentReceiptComponent', component: PaymentReceiptComponent },
   // { path: 'CreateNews', component: CreateNewsComponent },
   { path: 'SearchParty', component: SearchPartyComponent },
   { path: 'login', component: login },
   { path: 'dashboard', component: dashboardComponent },
   { path: 'logout', component: LogoutComponent },
   { path: 'manage_members', component: ManageMembersComponent },
   { path: 'manage_members/create', component: CreateManageMembersComponent },
   { path: 'manage_members/edit/:id', component: EditMembersComponent },
   { path: 'loader', component: LoaderComponent },
   { path: 'manage_agents', component: ManageAgentsComponent },
   { path: 'manage_agents/create', component: CreateManageAgentsComponent },
   { path: 'manage_agents/edit/:id', component: EditAgentsComponent },
   { path: 'payment_receipt', component: PaymentReceiptComponent },
   { path: 'serverComponent', component: ServerComponentComponent },
   { path: 'news', component: NewsComponent },
   { path: 'create_news', component: CreateNewsComponent }
];

@NgModule({
	imports:      [ BrowserModule ,RouterModule.forRoot(appRoutes),FormsModule,HttpModule,NG2DataTableModule,BsDatepickerModule.forRoot(),ReactiveFormsModule],
	declarations: [ AppComponent, HeaderComponent,OrderComponent,
					CreateorderComponent, ReceivePaymentComponent,
					PaymentReceiptComponent,
					SearchPartyComponent,login,navComponent,dashboardComponent,
          LogoutComponent,AlertComponent, ManageMembersComponent,
          CreateManageMembersComponent,EditMembersComponent, LoaderComponent, 
          ManageAgentsComponent,CreateManageAgentsComponent,EditAgentsComponent,
          ServerComponentComponent, NewsComponent,CreateNewsComponent
				  ],
   providers: [
           AuthGuard,
           AlertService,
           AuthenticationService,
           UserService,
           fakeBackendProvider,
           MockBackend,
           BaseRequestOptions,NameService,
           LoaderService,
           {provide: LocationStrategy, useClass: HashLocationStrategy}
    ],
	bootstrap:    [ AppComponent ]
})
export class AppModule {}


