import { Component, OnInit } from "@angular/core";
import { CommonService } from './../services/common.service';
import { Http } from "@angular/http";
import * as _ from "lodash";

@Component({
    selector: 'server',
    template: `



    <div class="col-xs-12 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2">

        <div class="page-header">
            <h1>
                <span>Angular2 DataTable component for server pagination</span><br/>
                <small>fork from <a href="https://github.com/mariuszfoltak/angular2-datatable">mariuszfoltak</a></small>
            </h1>
        </div>

        <div class="panel panel-default">
            <div class="panel-heading">User information</div>

            <table class="table table-striped" [mfData]="data" #mf="mfDataTable" [mfRowsOnPage]="rowsOnPage"
                [(mfSortBy)]="sortBy" [(mfSortOrder)]="sortOrder" [mfActivePage]="activePage" (mfOnPageChange)="onPageChange($event)"
                [mfIsServerPagination]="true" [(mfAmountOfRows)]="itemsTotal" (mfSortOrderChange)="onSortOrder($event)">
                <thead>
                    <tr>
                        <th style="width: 10%"></th>
                        <th style="width: 20%">
                            <mfDefaultSorter by="name">Name</mfDefaultSorter>
                        </th>
                        <th style="width: 40%">
                            <mfDefaultSorter by="email">Email</mfDefaultSorter>
                        </th>
                        <th style="width: 10%">
                            <mfDefaultSorter by="age">Age</mfDefaultSorter>
                        </th>
                        <th style="width: 20%">
                            <mfDefaultSorter [by]="sortByWordLength">City</mfDefaultSorter>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr *ngFor="let party of mf.data">
                        <td>
                            <button (click)="remove(item)" class="btn btn-danger">x</button>
                        </td>
                        <td>{{party.seller_name}}</td>
                        <td>{{party.agent_name}}</td> 
                        <td class="dark_red">{{party.diff}}</td>
                        <td class="dark_green">{{party.amount | number : fractionSize}}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                        <td colspan="5">
                            <mfBootstrapPaginator [rowsOnPageSet]="[5,10,15]"></mfBootstrapPaginator>
                        </td>
                    </tr>
                </tfoot>
            </table>
        </div>
    </div>



`,

  providers :[CommonService]
})
export class ServerComponentComponent implements OnInit {
    public data: any[];
    public filterQuery = "";
    public rowsOnPage = 1;
    public activePage = 1;
    public sortBy = "email";
    public sortOrder = "asc";
    public itemsTotal = 0;
    search :any;
    params: any = {};
    constructor(private http: Http, private _commonService:CommonService) {
    }

    
    ngOnInit(): void {
        this.loadData();
    }

    public loadData() {
        // this.http.get("http://localhost/tia_backend/Search_party")
        //     .subscribe((data) => {
        //         setTimeout(() => {
        //             this.data = _.orderBy(data.json(), this.sortBy, [this.sortOrder]);
        //             this.data = _.slice(this.data, this.activePage, this.activePage + this.rowsOnPage);
        //             this.itemsTotal = data.json().length;
        //         }, 1000);
        //     });

        
        this.search = '';

        this.params = {
            page: this.activePage,
            number_of_rows: this.rowsOnPage,
            search: '',
        }

        this._commonService.search_party(this.params).map(res => res.json())
        .toPromise().then((data) => {
                setTimeout(() => {
                    this.data = _.orderBy(data.result.parties, this.sortBy, [this.sortOrder]);
                    this.itemsTotal = data.result.total_record;
                    console.log(this.itemsTotal)
                }, 1000);
        })
        .catch((ex) => {
            console.log(ex);
        });

    

    }
    public toInt(num: string) {
        return +num;
    }

    public sortByWordLength = (a: any) => {
        return a.city.length;
    }

    public remove(item) {
        let index = this.data.indexOf(item);
        if (index > -1) {
            this.data.splice(index, 1);
        }
    }
    public onSortOrder(event) {
        this.loadData();
    }
    public onPageChange(event) {


        console.log(event);

        this.rowsOnPage = event.rowsOnPage;
        this.activePage = event.activePage;
        this.loadData();
    }

}


