import { Component, OnInit } from '@angular/core';
import 'rxjs/add/operator/toPromise';
import { Observable } from 'rxjs/Rx';
import { Router} from '@angular/router';
import { LoaderService } from './../services/loader.service';
import { CommonService } from './../services/common.service';
import { Http } from "@angular/http";
import * as _ from "lodash";




@Component({
  selector: 'my-app',
  templateUrl: './search_party.html',
  providers :[CommonService]
})
export class SearchPartyComponent implements OnInit{
	showLoader: boolean;
	dashboard = [];
	less_than_30 : number = 0;
	is_result_found: boolean = true;
    public data: any[];
    public filterQuery = "";
    public rowsOnPage = 10;
    public activePage = 1;
    // public sortBy = "email";
    // public sortOrder = "asc";
    public itemsTotal = 0;
    params: any = {};

	constructor(private _commonService:CommonService,  public router: Router,private loaderService: LoaderService){};

	ngOnInit() {
		jQuery('.main-title-header').html('SEARCH PARTY');
		this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
        this.loadData('');
	}

    public loadData(value) {
        this.loaderService.display(true);
        this.params = {
            page: this.activePage,
            number_of_rows: this.rowsOnPage,
            search: value,
        }
        console.log(this.params)
        this._commonService.search_party(this.params).map(res => res.json())
        .toPromise().then((data) => {
                	console.log(data)
                this.dashboard = data.result.dashboard;
                setTimeout(() => {
                    this.data = _.orderBy(data.result.parties);
                    this.itemsTotal = data.result.total_record;
                    this.loaderService.display(false);
                }, 1000);
        })
        .catch((ex) => {
            console.log(ex);
        });
    }
    public toInt(num: string) {
        return +num;
    }

    public sortByWordLength = (a: any) => {
        return a.city.length;
    }

    public remove(item) {
        let index = this.data.indexOf(item);
        if (index > -1) {
            this.data.splice(index, 1);
        }
    }
    public onSortOrder(event) {
        this.loadData('');
    }
    public onPageChange(event) {
        this.rowsOnPage = event.rowsOnPage;
        this.activePage = event.activePage;
        this.loadData('');
    }
}


