import { Component, OnInit, OnDestroy} from '@angular/core';
import { CommonModule } from '@angular/common';
import { CommonService } from './../services/common.service';
import { Router } from '@angular/router';
import { LoaderService } from './../services/loader.service';

@Component({
  	selector: 'nav-qq',
  	templateUrl: './create_order.html',
	providers :[CommonService]
	
})
export class CreateorderComponent {
	showLoader: boolean;
	Result = [];
	errors: any;
	data_list = [];
	agent_name: string = '';
	bill_date: string = '';
	today = Date.now();

	constructor(private _commonService:CommonService,private router: Router,private loaderService: LoaderService){
		this.loaderService.display(true);
		this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
         this.loaderService.display(false);
	};
	ngOnInit(){
		jQuery('.main-title-header').html('SALES ORDER');
		this.loaderService.display(true);
	    this._commonService.getAgentDetails().map(res => res.json())
	      .toPromise().then((data) => {
	        if(data.status == 'success'){
	          this.data_list = data.result;
	          this.loaderService.display(false);
	        }
	      })
	      .catch((ex) => {
	          console.log(ex);
      	});
	}
	submitorder(values){
		console.log(values)
		this.loaderService.display(true);
		let data = this._commonService.submitorder(values)
		.map(res => res.json())
    	.toPromise().then((data) => {
			if(data.status == 'failure'){
				this.errors = data.error;
				this.loaderService.display(false);
			}else if(data.status == 'success'){
				this.router.navigate(['/OrderComponent']);
			}
			
	    })
	    .catch((ex) => {
	        console.log(ex);
	    });
	}
}


