import { Component, OnInit } from '@angular/core';
import{ CommonService } from './../services/common.service';
import { Router } from '@angular/router';
import {ActivatedRoute} from '@angular/router';
import { LoaderService } from './../services/loader.service';
@Component({
  selector: 'app-manage-members',
  templateUrl: './manage-members.component.html',
  providers :[CommonService]
})
export class ManageMembersComponent implements OnInit {
  data_list = [];
  showLoader: boolean;
  public data: any[];
  status: string = ''
  constructor(private _commonService:CommonService,private router: Router,private loaderService:LoaderService) { }

  ngOnInit() {
    this.loaderService.display(true);
    this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
    this._commonService.getMemberDetails().map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
          this.data_list = data.result;
          this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
      });
    // console.log(this.data_list)
  }
  delete_members(id,event) {
    let target = event.target || event.srcElement || event.currentTarget;
    if(confirm("Are you sure you want to delete this agent")){
      let data = this._commonService.delete_members(id)
      .map(res => res.json())
        .toPromise().then((data) => {
          if(data.status == 'success'){
            jQuery(target).parents('tr').remove();
          }
        })
        .catch((ex) => {
            console.log(ex);
        });
      }
  }

}

@Component({
    selector: 'app-create-manage-members',
    templateUrl: './create.component.html',
  providers :[CommonService]
})
export class CreateManageMembersComponent implements OnInit {
  errors :any;
  status: string = '';
  showLoader: boolean;
  constructor(private _commonService:CommonService,private router: Router,private loaderService:LoaderService) { }

  ngOnInit() {
    this.loaderService.display(true);
    this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
    this.loaderService.display(false);
  }
  submitmember(formdata){
    this.loaderService.display(true);
    let data = this._commonService.submitmember(formdata)
    .map(res => res.json())
      .toPromise().then((data) => {
      if(data.status == 'failure'){
        this.errors = data.error;
        this.loaderService.display(false);
      }else if(data.status == 'success'){
        this.router.navigate(['/manage_members']);
      }

      })
      .catch((ex) => {
          console.log(ex);
      });
  }
}
@Component({
  	selector: 'edit-create-manage-members',
  	templateUrl: './edit.component.html',
	  providers :[CommonService]
})
export class EditMembersComponent implements OnInit {
	errors: any;
  id:string;
  user_data: any;
  showLoader: boolean;
  status: string = '';
  constructor(private _commonService:CommonService,private router: Router,private route: ActivatedRoute,private loaderService:LoaderService) { }

  ngOnInit() {
    this.loaderService.display(true);
    this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
    this.route.params.subscribe(params => {
        this.id = params["id"];
          this._commonService.getMemberDetailsById(this.id).map(res => res.json())
            .toPromise().then((data) => {
              if(data.status == 'success'){
                console.log(data);
                this.user_data = data.result;
                this.loaderService.display(false);
              }
            })
            .catch((ex) => {
                console.log(ex);
            });
        });
  }
  editmember(formdata){
    console.log(formdata)
    //this.loaderService.display(true);
    let data = this._commonService.editMember(formdata)
    .map(res => res.json())
      .toPromise().then((data) => {
      if(data.status == 'failure'){
        this.errors = data.error;
        this.loaderService.display(false);
      }else if(data.status == 'success'){
        this.router.navigate(['/manage_members']);
      }

      })
      .catch((ex) => {
          console.log(ex);
      });
  }
  delete() {
    alert('fdfdf')
  }
}






