import { Component, OnInit } from '@angular/core';
import{ CommonService } from './../services/common.service';
import { Router } from '@angular/router';
import {ActivatedRoute} from '@angular/router';
import { LoaderService } from './../services/loader.service';
@Component({
  selector: 'app-manage-agents',
  templateUrl: './manage-agents.component.html',
  providers :[CommonService]
})
export class ManageAgentsComponent implements OnInit {
  data_list = [];

  public data: any[];
	showLoader: boolean;
	constructor(private _commonService:CommonService,private router: Router,private loaderService:LoaderService) { }

	ngOnInit() {
  	this.loaderService.display(true);
    this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
    this._commonService.getAgentDetails().map(res => res.json())
      .toPromise().then((data) => {
        if(data.status == 'success'){
          this.data_list = data.result;
          this.loaderService.display(false);
        }
      })
      .catch((ex) => {
          console.log(ex);
      });
	}
  delete(id,event) {
    let target = event.target || event.srcElement || event.currentTarget;
    if(confirm("Are you sure you want to delete this agent")){
      let data = this._commonService.deleteagents(id)
      .map(res => res.json())
        .toPromise().then((data) => {
          if(data.status == 'success'){
            jQuery(target).parents('tr').remove();
          }
        })
        .catch((ex) => {
            console.log(ex);
        });
      }
  }

}
@Component({
    selector: 'app-create-manage-agents',
    templateUrl: './create.component.html',
  providers :[CommonService]
})
export class CreateManageAgentsComponent implements OnInit {
  errors : any;
  showLoader: boolean;
  status: string = '';
  constructor(private _commonService:CommonService,private router: Router,private loaderService:LoaderService) { }

  ngOnInit() {
    this.loaderService.display(true);
    this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
    this.loaderService.display(false);
  }
  submitagents(formdata){
    this.loaderService.display(true);
    let data = this._commonService.submitagents(formdata)
    .map(res => res.json())
      .toPromise().then((data) => {
      if(data.status == 'failure'){
        this.errors = data.error;
        this.loaderService.display(false);
      }else if(data.status == 'success'){
        this.router.navigate(['/manage_agents']);
      }

      })
      .catch((ex) => {
          console.log(ex);
      });
  }
}
@Component({
  	selector: 'edit-create-manage-members',
  	templateUrl: './edit.component.html',
	  providers :[CommonService]
})
export class EditAgentsComponent implements OnInit {
	errors :any;
  id:string;
  user_data: any;
  showLoader: boolean;
  status: string = '';
  constructor(private _commonService:CommonService,private router: Router,private route: ActivatedRoute,private loaderService:LoaderService) { }

  ngOnInit() {
    this.loaderService.display(true);
    this.loaderService.status.subscribe((val: boolean) => {
            this.showLoader = val;
        });
    this.route.params.subscribe(params => {
        this.id = params["id"];
          this._commonService.getAgentDetailsById(this.id).map(res => res.json())
            .toPromise().then((data) => {
              if(data.status == 'success'){
                console.log(data);
                this.user_data = data.result;
                this.loaderService.display(false);
              }
            })
            .catch((ex) => {
                console.log(ex);
            });
        });
  }
  editagent(formdata){
    console.log(formdata)
    //this.loaderService.display(true);
    let data = this._commonService.editagent(formdata)
    .map(res => res.json())
      .toPromise().then((data) => {
      if(data.status == 'failure'){
        this.errors = data.error;
        this.loaderService.display(false);
      }else if(data.status == 'success'){
        this.router.navigate(['/manage_agents']);
      }

      })
      .catch((ex) => {
          console.log(ex);
      });
  }
}


